.modelsDiv {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  gap: 3rem;
  align-items: center;
  text-align: center;
  padding: 10rem 0;
  width: 110rem;
  margin: 0 auto;
}
@media (max-width: 1150px) {
  .modelsDiv {
    grid-template-columns: 1fr 1fr;
    width: -moz-fit-content;
    width: fit-content;
  }
}
@media (max-width: 800px) {
  .modelsDiv {
    grid-template-columns: 1fr;
    width: -moz-fit-content;
    width: fit-content;
  }
}
.modelsDivBox {
  border: 1px solid #d5d5d5;
  border-radius: 2rem;
  display: flex;
  width: 35rem;
  flex-direction: column;
}
@media (max-width: 400px) {
  .modelsDivBox {
    grid-template-columns: 1fr;
    width: 100%;
  }
}
.modelsDivBoxImg {
  width: 100%;
  height: auto;
  border-radius: 2rem;
}
.modelsDivBoxImg img {
  width: 100%;
  height: 27rem;
  border-top-right-radius: 2rem;
  border-top-left-radius: 2rem;
}
.modelsDivBoxDescr {
  padding: 2rem 3rem;
  display: flex;
  flex-direction: column;
  color: #010103;
}
.modelsDivBoxDescrNamePrice {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.modelsDivBoxDescrNamePriceName {
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 0.5rem;
}
.modelsDivBoxDescrNamePriceName p {
  font-size: 2.4rem;
  font-weight: 700;
}
.modelsDivBoxDescrNamePriceName span {
  display: flex;
  gap: 0.4rem;
  font-size: 1.4rem;
  color: #ffc933;
}

.modelsDivBoxDescrNamePricePrice {
  display: flex;
  flex-direction: column;
  text-align: right;
}
.modelsDivBoxDescrNamePricePrice h4 {
  font-size: 2.8rem;
}
.modelsDivBoxDescrNamePricePrice p {
  font-size: 1.6rem;
}
.modelsDivBoxDescrNamePriceDetails {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
  -moz-column-gap: 7rem;
  column-gap: 7rem;
  margin-top: 2.5rem;
  margin: 2rem auto;
  padding-bottom: 2.5rem;
  border-bottom: 1px solid #c6c6c6;
}
.modelsDivBoxDescrNamePriceDetails span {
  font-size: 1.8rem;
  font-weight: 500;
  color: #777777;
  text-align: left;
}
.modelsDivBoxDescrNamePriceDetails span i {
  color: #010103;
}
.modelsDivBoxDescrNamePriceBtn {
  background-color: #656af8;
  padding: 1.8rem 3rem;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px 0 rgba(48, 131, 255, 0.35);
  transition: all 0.3s;
  border: 2px solid #656af8;
  font-size: 1.8rem;
  cursor: pointer;
}
.modelsDivBoxDescrNamePriceBtn:hover {
  box-shadow: 0 10px 15px 0 rgba(48, 131, 255, 0.6);
  background-color: #2e32ad;
}
.modelsDivBoxDescrNamePriceBtnA {
  text-decoration: none;
  color: white;
  font-weight: 700;
}
