.chooseSection {
    background-color: white;
    padding: 2rem 0 10rem 0;
    background-image: url("/src/assets/images/chooseUs/bg.png");
    background-position: -225px 255px;
    background-size: cover;
    background-repeat: no-repeat;
  }
  
  .chooseContainer {
    display: flex;
    flex-direction: column;
  }
  .chooseContainerImg {
    width: 90%;
    height: auto;
    margin: 0 auto;
  }
  @media (max-width: 550px) {
    .chooseContainerImg {
      width: 100%;
    }
  }
  
  .textContainer {
    display: flex;
    justify-content: space-around;
    margin-top: 3rem;
    width: 100%;
  }
  @media (max-width: 1000px) {
    .textContainer {
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 5.5rem;
    }
  }
  .textContainerLeft {
    text-align: left;
    display: flex;
    flex-direction: column;
    max-width: 50rem;
    color: #010103;
  }
  @media (max-width: 1000px) {
    .textContainerLeft {
      align-items: center;
      text-align: center;
    }
  }
  .textContainerLeft h4 {
    font-size: 2.2rem;
    margin-bottom: 0.7rem;
    font-weight: 600;
    font-family: "Rubik", sans-serif;
  }
  .textContainerLeft h2 {
    font-size: 4.2rem;
    line-height: 1.2;
    margin-bottom: 2rem;
  }
  .textContainerLeft p {
    font-size: 1.6rem;
    font-family: "Rubik", sans-serif;
    line-height: 1.5;
    color: #706f7b;
    margin-bottom: 3.3rem;
  }
  .textContainerLeft a {
    text-decoration: none;
    color: white;
    font-weight: 700;
    background-color: #656af8;
    padding: 1.5rem 2.5rem;
    border-radius: 0.3rem;
    box-shadow: 0 10px 15px 0 rgba(48, 131, 255, 0.35);
    transition: all 0.3s;
    border: 2px solid #656af8;
    font-size: 1.6rem;
    width: -moz-fit-content;
    width: fit-content;
  }
  .textContainerLeft a:hover {
    box-shadow: 0 10px 15px 0 rgba(48, 131, 255, 0.6);
    background-color: #2e32ad;
  }
  .textContainerRight {
    display: flex;
    flex-direction: column;
    gap: 4.5rem;
    max-width: 44rem;
  }
  .textContainerRightBox {
    display: flex;
  }
  @media (max-width: 550px) {
    .textContainerRightBox {
      flex-direction: column;
      align-items: center;
    }
  }
  .textContainerRightBox img {
    width: 11rem;
    height: 11rem;
    margin-right: 1.1rem;
  }
  .textContainerRightBoxText {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
  }
  .textContainerRightBoxText h4 {
    font-size: 2.4rem;
  }
  .textContainerRightBoxText p {
    font-size: 1.6rem;
    color: #706f7b;
    font-family: "Rubik", sans-serif;
    line-height: 1.3;
  }
  